@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
  font-size: 62.5%;

  //  
  &.css-scroll-fixed:not(.open-only-mob) {
    @include scroll-blocked; }

  &.css-scroll-fixed.open-only-mob {
    @include media('<tab-lg') {
      @include scroll-blocked; } } }

body {
  position: relative;
  @include font-normal;
  font-size: $font-size;
  line-height: $line-height;
  color: $text-color;
  min-width: $layout-min-width;
  background-color: $bg-color;
  background-position: center top;
  background-repeat: no-repeat;

  @include media('<tab-sm') {
    font-size: $font-size--mob;
    line-height: $line-height--mob; }

  &.home-page {
    background-color: white;
    background-image: url(../../img/bg-index-section-01.jpg); }

  &.bg-alt {
    background-color: white; }

  //  
  *::selection {
    background-color: $base-color;
    color: white; } }

.layout-spaces {
  @if nth($page-layout-space, 2) != 0 {
    @include padding-horizontal(nth($page-layout-space, 2)); }

  @include media('<tab-sm') {
    @if nth($page-layout-space--sm, 2) != 0 {
      @include padding-horizontal(nth($page-layout-space--sm, 2)); } }

  @include media('<mob-lg') {
    @if nth($page-layout-space--mob, 2) != 0 {
      @include padding-horizontal(nth($page-layout-space--mob, 2)); } } }

.layout-max {
  max-width: $layout-max-width;
  @include push-auto;

  @if nth($page-layout-space, 2) != 0 {
    &.layout-spaces {
        max-width: #{$layout-max-width + nth($page-layout-space, 2) * 2}; } } }

.layout-text {
  max-width: $text-layout-width;
  @include push-auto;

  @if nth($page-layout-space, 2) != 0 {
    &.layout-spaces {
      max-width: #{$text-layout-width + nth($page-layout-space, 2) * 2}; } } }

.layout-flood,
.layout-section {
  max-width: $section-layout-width;
  @include push-auto;

  @if nth($page-layout-space, 2) != 0 {
    &.layout-spaces {
      max-width: #{$section-layout-width + nth($page-layout-space, 2) * 2}; } } }

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  margin: 0 auto;
  min-height: 100vh;
  position: relative;

  @include media('<desk-lg') {
    overflow: hidden; } }
