@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* === helper classes == */
.mark {
  color: $mark-color;
  font-style: inherit;
  font-weight: inherit; }

.text-color-alt {
  color: $base-color; }

.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.float-left {
  float: left; }

.float-right {
  float: right; }

.nobr {
  white-space: nowrap; }

.hide-text {
  @include hide-text; }

.hide {
  display: none !important; }

.desktop-only {
  @include media('<tab-lg') {
    display: none; } }

.devices-only {
  @include media('>=tab-lg') {
    display: none; } }

.mobile-only {
  @include media('>=mob-lg') {
    display: none; } }

/* btn-app */
.btn-app {
  display: inline-block;
  vertical-align: top;
  background-color: black;
  background-repeat: no-repeat;
  height: 6.2rem;
  padding: 1rem 5rem 1rem 8rem;
  font-size: 1.8rem;
  line-height: 2.2rem;
  text-align: left;
  text-decoration: none;
  color: white;
  border-radius: 6.2rem;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  // transition
  $prop: background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  @include media('<tab-lg') {
    font-size: 1.5rem;
    line-height: 1.9rem;
    padding: 1.5rem 3rem 1.6rem 6.2rem;
    height: auto;
    border-radius: 5rem;

    & > br {
      display: none; } }

  &:hover {
    background-color: $highlight-color--hover; } }

.btn-app_google-play {
  background-image: url(../../img/app-a.png);
  background-position: 2.8rem center;
  background-size: 29px auto;

  @include media('<tab-lg') {
    background-position: 3.3rem center;
    background-size: 19px auto; } }

.btn-app_apple {
  background-image: url(../../img/app-ios.png);
  background-position: 28px center;
  background-size: 26px auto;

  @include media('<tab-lg') {
    background-position: 3rem center;
    background-size: 19px auto; } }

/* btn-app end */

/* show elements on scroll */
html:not(.no-js) {
  .show-figure-js {
    visibility: hidden; } }

/* show elements on scroll end */

/* social share */
.soc-share-box {
  position: sticky;
  top: 10px;
  pointer-events: none;
  z-index: 5;
  margin-top: calc(-#{$heading-bottom} - 2.9rem);
  padding-bottom: $heading-bottom;

  @include media('<desk-lg') {
    position: relative;
    top: auto;
    z-index: auto;
    margin-top: auto;
    margin-bottom: 5rem;
    padding-bottom: 0; }

  @include media('<tab-sm') {
    margin-bottom: 2rem; } }

.soc-share {
  pointer-events: auto; }

/* social share end */

/* chats */
.chats {
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  cursor: default;

  //+media('<mob-lg')
  //justify-content: center

  &:last-child {
    margin-bottom: 0; } }

.chats__group {
  margin-left: 4rem;

  //+media('<desk-lg')
  //margin-left: 1.5rem

  //+media('<tab-lg')
  //margin-left: 1rem

  &:first-child {
    margin-left: 0; }

  & > strong {
    display: block;
    margin-bottom: 0.7rem;
    @include font-thin;
    font-size: 1.4rem;
    line-height: 1.8rem; } }

.chats__list {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin: 0 -15px -15px 0;

  //+media('<tab-lg')
 }  //margin: 0 -10px -10px 0

.chats__item {
  float: left;
  margin: 0 15px 15px 0;

  a {
    display: block;
    position: relative;
    font-size: 60px;
    @include size(1em);
    color: inherit;

    //+media('<tab-lg')
    //font-size: 40px

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      @include size(100%);
      background-image: url("../../img/soc-sprite.svg");
      background-repeat: no-repeat;
      background-size: 12em auto; } //   , ..   12    , 1em - 1 

    &::after {
      opacity: 0; }

    // hover
    &:hover {
      &::before {
        opacity: 0; }

      &::after {
        opacity: 1; } }

    $soc-list: 'facebook', 'telegram', 'viber';
    $type-length: 4; //      ,        

    @for $i from 1 through length($soc-list) {
      $pos: $type-length - $type-length * $i * 1em; //      background

      //@debug $pos

      &.#{nth($soc-list, $i)} {
        &::before { //       (   -      : 1, 5, 9  ..)
          $p: $pos;
          background-position: $p $p; }

        &::after { //      (   -1em -      : 2, 6, 10  ..)
          $p: $pos - 1;
          background-position: $p $p; }

        .user-form__msg__soc & {
          &::before { //        (   -2em -      : 3, 7, 11  ..)
            $p: $pos - 2;
            background-position: $p $p; }

          &::after { //       (   -3em -      : 4, 8, 12  ..)
            $p: $pos - 3;
            background-position: $p $p; } } } }

    span { //  ,      
      @include hide-text; } } }

/* chats end */

/* === HEADER === */
.header {
  overflow-x: hidden;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  min-width: $layout-min-width;
  height: $header-height;
  z-index: $z-high;

  @include media('<tab-lg') {
    height: $header-height--sm; }

  @include media('<tab-sm') {
    height: $header-height--mob; }

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    opacity: 0;
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    .page-scrolled & {
      opacity: 1; } } }

//  ,     
.header_float {
  position: relative;
  left: auto;
  top: auto; }

.header-frame {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%; }

/* logo */
.logo {
  float: left;
  display: block;
  position: relative;
  background-image: url(../../img/logo.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 18.7rem;
  margin-bottom: 1.9rem;
  color: #cccddb;

  @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi) {
    background-image: url(../../img/logo-2x.png); }

  @include media('<tab-lg') {
    margin-bottom: 0.3rem; }

  @include media('<tab-sm') {
    width: 9.8rem;
    margin-bottom: 0; }

  //+media('<desk-lg')
  //background-image: url(../../img/logo-sm-2x.png)
  //width: 165px
  //margin-top: -13px

  //+media('<tab-lg')
  //width: 93px
  //margin-top: 0
  //color: #a7a7ce

  &::before {
    content: "";
    display: block;
    @include ratio(187, 39); }

  a, & > span {
    display: block;
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    text-indent: -9999px; } }

/* logo end */

/* navigation */
.nav-wrap {
  margin: 0.1rem 10rem 0 0;

  @include media('<tab-lg') {
    display: none; } }

.nav {
  position: relative; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  @include font-bold;
  font-size: 1.8rem;
  line-height: 2.4rem;
  text-transform: uppercase;
  letter-spacing: 0.01em;
  cursor: default;
  @include clear;

  @include media('<desk-md') {
    font-size: 1.6rem; }

  @include media('<desk-sm') {
    font-size: 1.4rem; }

  li {
    position: relative;
    float: left;
    margin-left: 5.7rem;

    @include media('<desk-md') {
      margin-left: 3.5rem; } }

  a {
    color: #000;
    text-decoration: none;
    transition: color $animation-speed;

    &:hover {
      color: $highlight-color--hover; } }

  // current
  li.current {
    color: $text-color-light;
    pointer-events: none;

    a, & > span {
      text-decoration: none;
      color: inherit;
      cursor: default; } } }

/* navigation end */

/* alternative navigation */
.alt-nav {
  position: relative; }

.alt-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  cursor: default;

  @include media('<tab-lg') {
    text-align: center; }

  li {
    @include font-bold;
    font-size: 5.5rem;
    line-height: 6.6rem;
    letter-spacing: 0.01em;
    margin-bottom: 4.1rem;
    overflow: hidden;

    @include media('<desk-lg') {
      margin-bottom: 2rem;
      font-size: 4rem;
      line-height: 5rem; }

    @include media('<desk-ml') {
      font-size: 2.8rem;
      line-height: 3.4rem; }

    &:last-child {
      margin-bottom: 0; } }

  a {
    color: #000;
    text-decoration: none;
    transition: color $animation-speed;
    padding-top: 0.5em;

    &:hover {
      color: $highlight-color--hover; } }

  // current
  li.current {
    color: $text-color-light;
    pointer-events: none;

    a, & > span {
      text-decoration: none;
      color: inherit;
      cursor: default; } } }

/* alternative navigation end */

/* === SHUTTER NAVIGATION === */
$switcher-nav-delay: 0.10s;
$switcher-nav-dur: 0.2s;
$toggle-nav-delay: $switcher-nav-delay + $switcher-nav-dur;
$toggle-nav-dur: 0.2s;
$toggle-nav-curtains-dur: 0.5s;
$toggle-nav-item-dur: 0.4s;
// =====================

/* shutter navigation switcher */
a.shutter-nav-switcher {
  $btn-nav-size: 60px;
  $btn-nav-lines-width: 35px;
  $btn-nav-lines-height: 5px;
  $btn-nav-lines-color: currentColor;
  // ----------------------------
  $btn-nav-lines-width--sm: 33px;
  $btn-nav-lines-height--sm: 3px;
  // ----------------------------
  $btn-nav-lines-width--mob: 23px;
  $btn-nav-lines-height--mob: 2px;
  // =====================

  position: absolute;
  right: nth($page-layout-space, 2);
  top: 50%;
  margin: -#{round-float($btn-nav-size / 2, 1) + 1} -1.2rem 0;
  color: #000;
  text-decoration: none;
  width: $btn-nav-size;
  height: $btn-nav-size;
  // transition
  $prop: color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $switcher-nav-dur;
    timing-function: ease-in-out; }

  @include media('<tab-lg') {
    margin-right: -1.4rem; }

  @include media('<tab-sm') {
    right: nth($page-layout-space--sm, 2);
    margin-right: -1.6rem; }

  @include media('<mob-lg') {
    right: nth($page-layout-space--mob, 2);
    margin-right: -1.9rem; }

  //      
  .touchevents & {
    user-select: none;
    -webkit-tap-highlight-color: transparent; }

  // , , 
  &::before, &::after, & > i {
    display: block;
    position: absolute;
    left: 50%;
    height: $btn-nav-lines-height;
    background-color: $btn-nav-lines-color;
    border-radius: $btn-nav-lines-height;
    // transition
    $prop: transform, opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $switcher-nav-dur;
      timing-function: cubic-bezier(0.45, 0.05, 0.55, 0.95); }

    @include media('<tab-lg') {
      height: $btn-nav-lines-height--sm; }

    @include media('<tab-sm') {
      height: $btn-nav-lines-height--mob; } }

  // , 
  &::before, &::after {
    content: "";
    width: $btn-nav-lines-width + 5;
    margin: 10px 0 11px -#{round($btn-nav-lines-width / 2) + 5};

    @include media('<tab-lg') {
      width: $btn-nav-lines-width--sm + 11;
      margin: 12px 0 11px -#{round($btn-nav-lines-width--sm / 2) + 11}; }

    @include media('<tab-sm') {
      width: $btn-nav-lines-width--mob + 7;
      margin: 8px 0 8px -#{round($btn-nav-lines-width--mob / 2) + 7}; } }

  // 
  &::before {
    bottom: 50%;
    transition-delay: 0s; }

  // 
  & > i {
    font-size: 0;
    line-height: 0;
    top: 50%;
    width: $btn-nav-lines-width;
    margin: -#{round($btn-nav-lines-height / 2)} 0 0 -#{round($btn-nav-lines-width / 2)};
    transition-delay: round-float($switcher-nav-delay / 2, 2);

    @include media('<tab-lg') {
      width: $btn-nav-lines-width--sm;
      margin: -#{round($btn-nav-lines-height--sm / 2 - 1)} 0 0 -#{round($btn-nav-lines-width--sm / 2)}; }

    @include media('<tab-sm') {
      width: $btn-nav-lines-width--mob;
      margin: -#{round($btn-nav-lines-height--mob / 2)} 0 0 -#{round($btn-nav-lines-width--mob / 2)}; } }

  // 
  &::after {
    top: 50%;
    transition-delay: $switcher-nav-delay; }

  // 
  & > span {
    @include hide-text; }

  // hover
  &:hover {
    color: $highlight-color--hover; }

  // opened
  &.shutter-nav_is-open {
    &::before, &::after, & > i {
      transform: translate(80px, 0) translateZ(0);
      opacity: 0; } } }

/* shutter navigation switcher end */

/* shutter navigation close */
.shutter-nav-close {
  position: absolute;
  width: 3.1rem;
  height: 3.1rem;
  top: 3.9rem;
  right: nth($page-layout-space, 2) + 0.2;
  font-size: 0;
  line-height: 0;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  $rotate: -20deg;
  transform: rotate($rotate);
  transform: rotate($rotate) translateZ(0);
  // transition
  $prop: transform, opacity, visibility;
  will-change: $prop;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  transition: {
    property: $prop;
    //      (   ),  
    duration: 0s;
    delay: $toggle-nav-dur;
    timing-function: ease-in-out; }

  @include media('<tab-lg') {
    display: block;
    transition-delay: 0s;
    top: 2.5rem; }

  @include media('<tab-sm') {
    top: 1.5rem;
    right: nth($page-layout-space--sm, 2) + 0.2; }

  @include media('<mob-lg') {
    right: nth($page-layout-space--mob, 2) + 0.2; }

  &::before, &::after {
    $nav-close-cross-size: 5px 41px;
    $nav-close-cross-size--sm: 3px 41px;
    $nav-close-cross-size--mob: 2px 28px;
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: nth($nav-close-cross-size, 1);
    width: nth($nav-close-cross-size, 2);
    margin: -#{round(nth($nav-close-cross-size, 1) / 2)} 0 0 -#{round(nth($nav-close-cross-size, 2) / 2)};
    border-radius: nth($nav-close-cross-size, 1);
    background-color: white;
    // transition
    $prop: background-color;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    @include media('<tab-lg') {
      background-color: #000;
      height: nth($nav-close-cross-size--sm, 1);
      width: nth($nav-close-cross-size--sm, 2);
      margin: -#{round(nth($nav-close-cross-size--sm, 1) / 2)} 0 0 -#{round(nth($nav-close-cross-size--sm, 2) / 2)};
      border-radius: nth($nav-close-cross-size--sm, 1); }

    @include media('<tab-sm') {
      height: nth($nav-close-cross-size--mob, 1);
      width: nth($nav-close-cross-size--mob, 2);
      margin: -#{round(nth($nav-close-cross-size--mob, 1) / 2)} 0 0 -#{round(nth($nav-close-cross-size--mob, 2) / 2)};
      border-radius: nth($nav-close-cross-size--mob, 1); } }

  &::before {
    transform: rotate(45deg); }

  &::after {
    transform: rotate(-45deg); }

  &:hover {
    &::before, &::after {
      background-color: $highlight-color--hover; } }

  &.shutter-nav_is-open {
    opacity: 1;
    pointer-events: auto;
    visibility: visible;
    $rotate: 0deg;
    transform: rotate($rotate);
    transform: rotate($rotate) translateZ(0);
    transition: {
      //       
      duration: $toggle-nav-item-dur;
      delay: $toggle-nav-delay + $toggle-nav-curtains-dur - 0.05s; }

    @include media('<tab-lg') {
      transition: {
        duration: $animation-speed;
        delay: $animation-speed; } } } }

.shutter-nav .shutter-nav-close {
  @include media('<tab-lg') {
    display: none; } }

.shutter-nav-close_device {
  position: fixed;
  display: none;
  z-index: 10000;

  @include media('<tab-lg') {
    display: block; } }

/* shutter navigation close end */

/* shutter navigation overlay */
.shutter-overlay {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(black, 0.35);
  z-index: 9998;
  opacity: 0;
  pointer-events: none;
  // transition
  $prop: opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  @include media('<tab-lg') {
    display: block; }

  &.shutter-nav_is-open {
    opacity: 1; } }

/* shutter navigation overlay end */

/* shutter navigation */
.shutter-nav {
  position: fixed;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 9999;
  //      ,  
  transition: {
    property: opacity, visibility;
    duration: $toggle-nav-dur; }

  @include media('<tab-lg') {
    display: block;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: white;
    $translate: 100%, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);
    transition: {
      property: opacity, visibility, transform;
      duration: $animation-speed; } }

  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    z-index: -1;
    // transition
    $prop: opacity, visibility, transform;
    will-change: $prop;
    transition: {
      property: $prop;
      //      (   ),  
      duration: 0s;
      delay: $toggle-nav-dur;
      timing-function: cubic-bezier(0.4, 0.6, 0.2, 1); }

    @include media('<tab-lg') {
      display: none;
      content: none; } }

  &::before {
    background-color: white;
    $translate: 0, 100%;
    transform: translate($translate);
    transform: translate3d($translate, 0); }

  &::after {
    background-color: $base-color;
    left: 50%;
    $translate: 0, -100%;
    transform: translate($translate);
    transform: translate3d($translate, 0); }

  &.shutter-nav_is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;

    @include media('>=tab-lg') {
      //        
      transition: {
        duration: 0s;
        delay: 0s; } }

    @include media('<tab-lg') {
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0); }

    &::before, &::after {
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
      //       
      transition: {
        duration: $toggle-nav-curtains-dur;
        delay: $toggle-nav-delay; } } } }

.shutter-nav__layout {
  display: flex;
  align-items: flex-start;
  width: 100%;

  @include media('<tab-lg') {
    display: block;
    padding: 0; } }

.shutter-nav__content,
.shutter-nav__footer {
  flex-grow: 1;
  flex-basis: 50%;
  max-width: 50%;
  padding: 3rem 9rem;
  opacity: 0;
  will-change: transform, opacity;
  transition: {
    property: transform, opacity;
    //      (   ),  
    duration: 0s;
    delay: $toggle-nav-dur;
    timing-function: ease-out; }

  @include media('<desk-md') {
    padding: 2rem 4rem; }

  @include media('<tab-lg') {
    width: auto;
    max-width: none;
    padding: 5rem #{nth($page-layout-space, 2)};
    opacity: 1; }

  @include media('<tab-sm') {
    padding: 5rem #{nth($page-layout-space--sm, 2)}; }

  @include media('<mob-lg') {
    padding: 5rem #{nth($page-layout-space--mob, 2)}; }

  .shutter-nav_is-open & {
    @include media('>=tab-lg') {
      opacity: 1;
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
      transition: {
        //       
        duration: $toggle-nav-item-dur;
        delay: $toggle-nav-delay + $toggle-nav-curtains-dur - 0.05s; } } } }

//  
.shutter-nav__content {
  @include media('>=tab-lg') {
    $translate: 0, 4rem;
    transform: translate($translate);
    transform: translate3d($translate, 0);
    padding-left: 0; }

  @include media('<tab-lg') {
    padding-top: $header-height--sm;
    padding-bottom: $header-height--sm; }

  @include media('<tab-sm') {
    padding-top: $header-height--mob;
    padding-bottom: $header-height--mob; } }

//  
.shutter-nav__footer {
  color: white;

  @include media('>=tab-lg') {
    $translate: 0, -4rem;
    transform: translate($translate);
    transform: translate3d($translate, 0);
    padding-right: 0; }

  @include media('<tab-lg') {
    background-color: $base-color; } }

/* title */
.shutter-nav__title {
  @include font-extra-bold;
  font-size: 5.5rem;
  line-height: 6.6rem;
  letter-spacing: 0.01em;
  margin-bottom: 5.5rem;

  @include media('<desk-lg') {
    margin-bottom: 4rem;
    font-size: 4rem;
    line-height: 5rem; }

  @include media('<desk-ml') {
    font-size: 2.8rem;
    line-height: 3.4rem; }

  @include media('<tab-lg') {
    display: none; }

