/* lacerta app */
.icon-app {
  overflow: hidden;
  position: fixed;
  right: nth($page-layout-space, 2);
  top: 50%;
  $size: 6rem;
  width: $size;
  height: $size;
  margin-top: -#{round-float($size / 2, 1)};
  color: white;
  text-indent: -9999px;
  //background-color: $highlight-color
  @include linear-gradient(to bottom, $highlight-color 0, $highlight-color 50%, $btn-bg-hover-color 50%, $btn-bg-hover-color 100%);
  background-size: 100% 200%;
  box-shadow: 0 3px 8px rgba(black, 0.11);
  border-radius: 50%;
  z-index: $z-middle;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  // transition
  $prop: background-position, color, box-shadow;
  transition: {
    property: $prop;
    duration: 0.35s;
    timing-function: cubic-bezier(0.4, 0, 0.2, 1); }

  @include media('<desk-lg') {
    margin-right: -#{round-float($size / 2, 1)}; }

  @include media('<tab-lg') {
    top: $header-height--sm;
    margin-top: nth($page-layout-space, 2); }

  @include media('<tab-sm') {
    right: nth($page-layout-space--sm, 2);
    $size: 5rem;
    width: $size;
    height: $size;
    top: $header-height--mob;
    margin-top: nth($page-layout-space--sm, 2);
    margin-right: -#{round-float($size / 2, 1)}; }

  @include media('<mob-lg') {
    right: nth($page-layout-space--mob, 2);
    $size: 4rem;
    width: $size;
    height: $size;
    margin-top: nth($page-layout-space--mob, 2);
    margin-right: -#{round-float($size / 4, 1)}; }

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-image: url('data:image/png;base64,');
    background-repeat: no-repeat;
    background-position: center;

    @include media('<tab-sm') {
      background-size: 2rem; }

    @include media('<mob-lg') {
      background-size: 1.5rem; } }

  .no-touchevents &:hover {
    //background-color: $highlight-color--hover
    background-position: 0 100%;
    box-shadow: 0 3px 8px rgba($btn-bg-hover-color, 0.25); }

  &:active {
    margin-top: -#{round-float($size / 2, 1) - 0.1};
    box-shadow: 0 1px 4px rgba($btn-bg-hover-color, 0.25); } }

/* lacerta app end */

