  background-size: cover;

  @include media('<desk-sm') {
    padding-top: 7rem;
    padding-bottom: 8rem; } }

.manual__holder {
  position: relative;
  padding: 1rem 0 3.4rem;
  @include clear;

  @include media('<tab-lg') {
    padding: 0; } }

.manual__title {
  float: left;
  clear: left;
  width: 50%;
  @include font-extra-bold;
  font-size: $font-size-h1;
  line-height: $line-height-h1;
  color: white;
  margin-bottom: 5rem;

  @include media('<desk-ml') {
    width: 60%; }

  @include media('<desk-sm') {
    font-size: $font-size-h1--sm;
    line-height: $line-height-h1--sm;
    margin-bottom: 4rem; }

  @include media('<tab-lg') {
    width: auto;
    float: none;
    text-align: center;
    margin-bottom: 2.3rem; }

  &:last-child {
    margin-bottom: 0; } }

// --> manual steps <==
.manual__steps {
  float: left;
  clear: left;
  width: 50%;
  padding-bottom: 4.2rem;

  //     
  @include media('>=tab-lg') {
    &.swiper-container {
      margin: 0;
      position: static;
      overflow: visible;
      z-index: auto; }

    .swiper-wrapper {
      transform: none !important;
      display: block;
      position: static;
      width: auto;
      height: auto;
      z-index: auto;
      box-sizing: border-box; }

    .swiper-slide {
      position: static;
      height: auto; }

    .swiper-pagination {
      display: none; } }

  //      END

  @include media('<desk-ml') {
    width: 60%; }

  @include media('<tab-lg') {
    float: none;
    width: auto;
    margin: 0 -#{nth($page-layout-space, 2)}; }

  @include media('<tab-sm') {
    margin: 0 -#{nth($page-layout-space--sm, 2)}; }

  @include media('<mob-lg') {
    margin: 0 -#{nth($page-layout-space--mob, 2)}; } }

.manual__step {
  $root-elem: &;
  display: block;
  margin-bottom: 2rem;

  @include media('<tab-lg') {
    margin-bottom: 0;
    padding: 0 #{nth($page-layout-space, 2)}; }

  @include media('<tab-sm') {
    padding: 0 #{nth($page-layout-space--sm, 2)}; }

  @include media('<mob-lg') {
    padding: 0 #{nth($page-layout-space--mob, 2)}; }

  &:last-child {
    margin-bottom: 0; }

  @at-root {
    .manual__step-caption {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      justify-content: center;
      position: relative;
      min-height: 17rem;
      padding: 1.7rem 4rem 2rem 16.2rem;
      text-decoration: none;
      color: inherit;
      cursor: pointer;
      background-color: rgba(white, 0.15);
      border: 1px solid transparent;
      border-radius: 1rem;
      -webkit-tap-highlight-color: transparent;
      $prop: background-color, border-color;
      transition: {
        property: $prop;
        duration: $animation-speed;
        timing-function: ease-in-out; }

      .touchevents & {
        outline: none; }

      @include media('<desk-md') {
        padding: 1.7rem 3rem 2rem 12rem; }

      @include media('<tab-lg') {
        display: block;
        padding: 0;
        min-height: 0;
        border: none;
        background: none;
        cursor: default;
        border-radius: 0;
        margin-bottom: 3.4rem; }

      &:last-child {
        margin-bottom: 0; }

      // hover
      &:hover {
        border-color: #fff; }

      // current
      #{$root-elem}.current & {
        cursor: default;
        border-color: #fff;
        background-color: #fff;

        @include media('<tab-lg') {
          background: none;
          border-color: transparent; } } }

    .manual__step-title {
      min-width: 100%;
      @include font-bold;
      font-size: 2.4rem;
      line-height: 3.0rem;
      margin-bottom: 2.1rem;
      color: white;
      transition: color $animation-speed;

      @include media('<desk-sm') {
        font-size: 2.2rem;
        line-height: 3.0rem;
        margin-bottom: 1.3rem; }

      @include media('<tab-lg') {
        text-align: center; }

      &:last-child {
        margin-bottom: 0; }

      // current
      #{$root-elem}.current & {
        color: $title-color;

        @include media('<tab-lg') {
          color: white; } } }

    .manual__step-number {
      display: block;
      position: absolute;
      left: 4.6rem;
      top: 50%;
      $size: 8.3rem;
      width: $size;
      height: $size;
      margin-top: -#{round-float($size / 2, 1)};
      @include font-bold;
      font-size: 3.6rem;
      line-height: $size;
      text-align: center;
      color: white;
      background-color: $highlight-color;
      border-radius: 50%;

      @include media('<desk-md') {
        left: 2rem; }

      @include media('<tab-lg') {
        display: inline-block;
        vertical-align: top;
        $size: 2.1rem;
        width: $size;
        height: $size;
        position: relative;
        left: auto;
        top: auto;
        margin: 0.3rem 1.4rem 0 0;
        font-size: 1.6rem;
        line-height: $size - 0.1;
        padding: 0.1rem 0 0 0.1rem; } }

    .manual__step-text {
      min-width: 100%;
      font-size: 1.8rem;
      line-height: 2.2rem;
      margin-bottom: 2rem;
      color: white;
      transition: color $animation-speed;

      @include media('<tab-lg') {
        font-size: 2.0rem;
        line-height: 2.4rem;
        text-align: center; }

      &:last-child {
        margin-bottom: 0; }

      // current
      #{$root-elem}.current & {
        color: $text-color-alt;

        @include media('<tab-lg') {
          color: white; } } }

    .manual__step-figure {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      right: 0.2rem;
      top: 0;
      bottom: 0;
      width: 43.8rem;
      max-width: 50%;
      padding-left: 4rem;
      opacity: 0;
      pointer-events: none;
      // transition
      $prop: opacity;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed; }

      @include media('<desk-ml') {
        max-width: 40%; }

      @include media('<tab-lg') {
        position: relative;
        right: auto;
        top: auto;
        bottom: auto;
        padding-left: 0;
        height: 60.9rem;
        max-width: none;
        width: auto;
        opacity: 1;
        pointer-events: auto; }

      // current
      #{$root-elem}.current & {
        opacity: 1;
        pointer-events: auto; }

      img {
        max-width: 100%;
        max-height: 100%; } } } }

.manual__steps-pagination {
  .swiper-pagination-bullet {
    background: $highlight-color;

    &.swiper-pagination-bullet-active {
      background-color: white;
      opacity: 1; } } }

/* manual end */

/* bond */
$bond-space: 3.1rem 2.9rem 2.3rem;
$bond-space--sm: 2.4rem 2rem 1.9rem;
.bond {
  display: block;
  position: relative;
  padding: $bond-space;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  background-color: white;
  border-radius: 0.8rem;
  //box-shadow: 0 1rem 4.6rem rgba(#7c90d4, 0.25)
  box-shadow: 0 1rem 4.6rem rgba($box-shadow-color, 0.25);
  transition: box-shadow $animation-speed;

  @include media('<tab-lg') {
    padding: $bond-space--sm; } }

.bond__figure {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 15rem;
  height: 3.5rem;
  margin: 0.3rem 0 1.9rem;

  @include media('<tab-lg') {
    height: 2.9rem;
    width: 12rem;
    margin: 0.3rem 0 1.3rem; }

  img {
    display: block;
    max-width: 100%;
    max-height: 100%; } }

.bond__title {
  $font-size: 1.8rem;
  $line-height: 2.4rem;
  $line-length: 2;
  display: block;
  position: relative;
  @include font-normal;
  font-size: $font-size;
  line-height: $line-height;
  font-weight: normal;
  color: #0d0d0d;
  height: $line-height * $line-length;
  overflow: hidden;
  margin-bottom: 1.2rem;

  @include media('<tab-lg') {
    margin-bottom: 0.1rem; }

  &::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 20%;
    min-width: 6rem;
    height: $line-height;
    @include linear-gradient(to right, rgba(white, 0) 0%, white 100%); } }

.bond__release {
  overflow: hidden;
  position: relative;
  margin: 0 12rem 0.1rem 0;
  font-size: 1.8rem;
  line-height: 2.4rem;
  color: #0d0d0d;
  text-overflow: ellipsis;

  @include media('<tab-lg') {
    font-size: 1.8rem;
    line-height: 2.2rem; } }

.bond__term {
  overflow: hidden;
  position: relative;
  margin-right: 12rem;
  font-size: 2.4rem;
  line-height: 2.8rem;
