$yield-box-height: 71.4rem;
$yield-box-height--sm: 53.6rem;
$yield-box-spaces: 3.9rem 0;
.yield-box {
  position: relative;
  display: flex;
  height: $yield-box-height;

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none;
    overflow: hidden; }

  @include media('<tab-lg') {
    display: block;
    height: $yield-box-height--sm; } }

$yield-box-aside-width: 45.2rem;
$yield-box-aside-width--sm: 35rem;
$yield-box-aside-space: 6.1rem;
$yield-box-aside-space--sm: 2.0rem;
.yield-box__aside {
  flex-basis: $yield-box-aside-width;
  max-width: $yield-box-aside-width;
  height: 100%;
  margin-right: $yield-box-aside-space;

  @include media('<desk-sm') {
    flex-basis: $yield-box-aside-width--sm;
    max-width: $yield-box-aside-width--sm;
    margin-right: $yield-box-aside-space--sm; }

  @include media('<tab-lg') {
    position: absolute;
    left: 0;
    top: 0;
    overflow: hidden;
    height: 1px;
    width: 1px; } }

$yield-thumbs-elem-height: 22.3rem;
.yield-thumbs {
  $padding-bottom: 3.9rem;
  $padding-top: 3.9rem;
  position: relative;
  height: 100%;
  padding: 0 3rem;
  margin: 0 -3rem;

  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: $yield-thumbs-elem-height;
    pointer-events: none;
    left: 0;
    z-index: 5; }

  &::before {
    top: 0;
    @include linear-gradient(to bottom, $bg-color 0%, rgba($bg-color, 0) 100%); }

  &::after {
    bottom: 0;
    @include linear-gradient(to top, $bg-color 0%, rgba($bg-color, 0) 100%); } }

.yield-thumbs__item {
  opacity: 0.6;
  height: $yield-thumbs-elem-height;
  // transition
  $prop: opacity, background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  &.swiper-slide-active {
    opacity: 1; }

  &.swiper-slide-thumb-active {
    background-color: $highlight-color;
    opacity: 1;
    cursor: default;

    .bond__rate {
      background-color: white;
      color: $highlight-color; }

    .bond__title {
      &::after {
        display: none; } } } }

.yield-box__main {
  position: relative;
  flex-grow: 1;
  $width: calc(100% - #{$yield-box-aside-width} - #{$yield-box-aside-space});
