    max-width: $width; }

  @include media('<tab-lg') {
    max-width: none;
    padding: 0; } }

.yield-cards {
  height: 100%;
  position: relative;
  margin: 0 -2rem;
  padding: 0 2rem;
  //$padding-bottom: 2.2rem
  //$padding-top: 1rem
  //padding: #{$padding-top} 1.1rem #{$padding-bottom}
  //margin: -#{$padding-top} -1.1rem -#{$padding-bottom}

  @include media('<tab-lg') {
    $space: nth($page-layout-space, 2);
    padding: 0 #{$space};
    margin: 0 -#{$space}; }

  @include media('<tab-sm') {
    $space: nth($page-layout-space--sm, 2);
    padding: 0 #{$space};
    margin: 0 -#{$space}; }

  @include media('<mob-lg') {
    $space: nth($page-layout-space--mob, 2);
