// news variables
$news-elem-space-outer: 3rem 6.6rem;
$news-elem-space-outer--sm: 3rem 3rem;
$news-elem-space-inner: 2.7rem 3.6rem;
//$news-elem-img-space-h: 3.6rem
//$news-elem-img-space-v: 3rem

/* news preview element */
.news-elem {
  $element: &;
  display: block;
  background-color: white;
  text-decoration: none;
  color: inherit;
  @include clear;

  @at-root {
    .news-elem__figure {
      width: 33.3rem;
      background-repeat: no-repeat;
      background-position: center;
      background-color: $border-color;
      background-size: cover;
      font-size: 0;
      line-height: 0;

      &::before {
        content: "";
        display: block;
        @include ratio(1, 1); } }

    .news-elem__content {
      overflow: hidden;
      padding: nth($news-elem-space-inner, 1) nth($news-elem-space-inner, 2); }

    .news-elem__title {
      position: relative;
      overflow: hidden;
      @include font-bold;
      font-size: 2.4rem;
      line-height: 2.9rem;
      color: $title-color;
      margin-bottom: 1.7rem;
      padding-bottom: 2rem;
      transition: color $animation-speed;

      @include media('<desk-md') {
        font-size: 2.2rem;
        line-height: 2.6rem; }

      @include media('<desk-sm') {
        font-size: 2.0rem;
        line-height: 2.4rem; }

      &::before, &::after {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 24rem;
        max-width: 80%;
        border-top: 3px solid $border-color;

        @include media('<desk-md') {
          max-width: none;
          width: 100%; } }

      &::after {
        border-color: $highlight-color--hover;
        $translate: -100%, 0;
        transform: translate($translate);
        transform: translate3d($translate, 0);
        // transition
        $prop: transform;
        will-change: $prop;
        transition: {
          property: $prop;
          duration: $animation-speed;
          timing-function: ease-in-out; } }

      // hover
      #{$element}:hover & {
        color: $highlight-color--hover;

        &::after {
          $translate: 0, 0;
          transform: translate($translate);
          transform: translate3d($translate, 0); } } }

    .news-elem__text {
      font-size: 1.8rem;
      line-height: 2.2rem;
      color: $text-color-alt;

      //+media('<desk-md')
      //font-size: 1.6rem
 } } }      //line-height: 2.0rem

// type vertical
.type-v {
  @include media('<desk-md') {
    display: none; }

  .news-elem {
    padding: 0;
    box-shadow: none;
    min-height: 69.6rem; } // !Warning

  .news-elem__figure {
    width: 100%;

    &:last-child {
      margin-bottom: 0; } }

  //.news-elem__content
 }  //padding: $news-elem-img-space-v nth($news-elem-space-inner, 2) nth($news-elem-space-inner, 1)

// type left
.type-l {
  .news-elem {
    box-shadow: inset 0 0 0 1px $border-color; }

  .news-elem__figure {
    float: left; }

  //.news-elem__content
 }  //padding: #{nth($news-elem-space-inner, 1)} nth($news-elem-space-inner, 2) nth($news-elem-space-inner, 1) $news-elem-img-space-h

// type right
.type-r {
  .news-elem {
    box-shadow: inset 0 0 0 1px $border-color; }

  .news-elem__figure {
    float: right; }

  //.news-elem__content
 }  //padding: nth($news-elem-space-inner, 1) $news-elem-img-space-h nth($news-elem-space-inner, 1) nth($news-elem-space-inner, 2)

/* news preview element end */

/* news */
.news {
  padding-bottom: 16.1rem;

  @include media('<desk-sm') {
    padding-bottom: 7rem; } }

// news list
.news__list {
  margin: 0 -#{round-float(nth($news-elem-space-outer, 2) / 2, 1)} 3rem;
  @include clear;

  @include media('<desk-md') {
    margin: 0 -#{round-float(nth($news-elem-space-outer--sm, 2) / 2, 1)} 3rem; }

