// news footer
.news__footer {
  text-align: right; }

/* news end */

/* companies */
.section_companies {
  position: relative; }

$companies-filters-item-space: 3.6rem 3.6rem;
.companies-filters {
  background-color: white;
  padding-bottom: 8.2rem;

  @include media('<desk-sm') {
    padding-bottom: 7rem; } }

.companies-filters__row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -#{round-float(nth($companies-filters-item-space, 2) / 2, 1)} -#{round-float(nth($companies-filters-item-space, 1) / 2, 1)}; }

.companies-filters__item {
  flex-grow: 1;
  flex-basis: calc(33.3% - #{nth($companies-filters-item-space, 2)});
  max-width: 31rem;
  margin: 0 #{round-float(nth($companies-filters-item-space, 2) / 2, 1)} #{round-float(nth($companies-filters-item-space, 1) / 2, 1)}; }

.companies {
  padding-top: 8.2rem;
  padding-bottom: 15.6rem;
  background-color: $bg-color;

  @include media('<desk-sm') {
    padding-top: 7rem;
    padding-bottom: 7rem; } }

// companies list
$companies-elem-space-outer: 2.2rem 2.2rem;
.companies__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -#{round-float(nth($companies-elem-space-outer, 2) / 2, 1)} -#{nth($companies-elem-space-outer, 1)};
  @include clear; }

.companies__list-item {
  $width: 33.33%;
  flex-basis: $width;
  max-width: $width;
  padding: 0 #{round-float(nth($companies-elem-space-outer, 2) / 2, 1)} nth($companies-elem-space-outer, 1);

  @include media('<desk-sm') {
    $width: 50.0%;
    flex-basis: $width;
    max-width: $width;

    &:nth-child(n + 7) {
      display: none; } } }

// companies footer
.companies__footer {
  text-align: right; }

/* companies end */

/* company */
.company {
  padding-bottom: 16.1rem;

  @include media('<desk-sm') {
    padding-bottom: 7rem; } }

$company-box-aside-width: 44.7rem;
$company-box-aside-width--sm: 35rem;
.company-box {
  position: relative;
  display: flex;
  background-color: white;
  box-shadow: 0 1rem 4.6rem rgba($box-shadow-color, 0.25);
  border-radius: 8px;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: $company-box-aside-width;
    border-right: 1px solid #cfd2df;
    pointer-events: none;

    @include media('<desk-md') {
      left: $company-box-aside-width--sm; } } }

.company-box__aside {
  position: relative;
  flex-basis: $company-box-aside-width;
  max-width: $company-box-aside-width;
  padding-right: 6.1rem;

  @include media('<desk-ml') {
    padding-right: 4rem; }

  @include media('<desk-md') {
    flex-basis: $company-box-aside-width--sm;
    max-width: $company-box-aside-width--sm; } }

.company-box__main {
  position: relative;
  flex-grow: 1;
  $width: calc(100% - #{$company-box-aside-width});
  flex-basis: $width;
  max-width: $width;
  min-height: 64.4rem;

  @include media('<desk-md') {
    $width: calc(100% - #{$company-box-aside-width--sm});
    flex-basis: $width;
    max-width: $width; }

  .bond-calc {
    height: 100%; } }

/* company end */

/* app-visual */
.app-visual {
  position: relative;
  background: #622ee9 url(../../img/bg-app-offer.jpg) no-repeat center top fixed;
  background-size: cover;

  .heading + & {
    margin-top: 15.8rem;

    @include media('<desk-sm') {
      margin-top: 7rem; } } }

.app-visual__holder {
  padding-top: 7.9rem;
  padding-bottom: 7.8rem;
  min-height: 70rem;

  @include media('<desk-sm') {
    padding-top: 7rem;
    padding-bottom: 7rem;
    min-height: 50rem; } }

.app-visual__slider {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: url(../../img/app-visual-desktop.png) no-repeat center;
  background-size: auto 54.3rem;

  @include media('<desk-sm') {
    background-size: auto 36rem; } }

/* app-visual end */

/* download */
.section_download {
  padding-top: 13.8rem;
  padding-bottom: 15.2rem;

  @include media('<desk-sm') {
    padding-top: 7rem;
    padding-bottom: 7rem; } }

$download-layout: 67rem;
.download-layout {
  max-width: $download-layout;
  margin: 0 auto; }

.download__title {
  @include font-bold;
  font-size: $font-size-h1;
  line-height: $line-height-h1;
  color: #000;
  text-align: center;
  margin-bottom: 4.2rem;

  @include media('<desk-sm') {
    font-size: $font-size-h1--sm;
    line-height: $line-height-h1--sm;
    margin-bottom: 1.5rem; }

  &:last-child {
    margin-bottom: 0; } }

.download__text {
  font-size: 1.8rem;
  line-height: 2.2rem;
  margin-bottom: 5.3rem;
  text-align: center;

  @include media('<desk-sm') {
    margin-bottom: 2rem; }

  &:last-child {
    margin-bottom: 0; }

  p {
    margin-bottom: 2.1rem;

    &:last-child {
      margin-bottom: 0; } } }

.download__text-entry {
  color: $text-color-alt; }

.download__text-highlight {
  color: $highlight-color; }

$download-app-spaces: 3.6rem;
.download__app {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin: 0 -#{round-float($download-app-spaces / 2, 1)} 3.3rem;
  text-align: center;

  @include media('<desk-sm') {
    margin-bottom: 0; }

  &:last-child {
    margin-bottom: 0; } }

.download__app-item {
  margin: 0 #{round-float($download-app-spaces / 2, 1)} 2rem; }

.download__note {
  font-size: 1.9rem;
  line-height: 2.4rem;
  color: rgba(#1e202b, 0.8);

  @include media('<desk-sm') {
    font-size: 1.6rem;
    line-height: 2.4rem;
    text-align: center; } }

/* download end */
