@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  padding: 0;
  color: $title-color;
  margin: 0 0 1.6rem;

  @include media('<tab-sm') {
    margin-bottom: $bottom-space--mob; }

  &:last-child {
    margin-bottom: 0; }

  a {
    text-decoration: none;
    color: inherit;

    .no-touchevents &:hover {
      color: $base-color;
      text-decoration: underline; } } }

h1, .h1 {
  @include font-extra-bold;
  @include font-h1;
  margin-bottom: 6.2rem;

  @include media('<desk-sm') {
    margin-bottom: 1.3rem; }

  @include media('<tab-sm') {
    margin-bottom: 1.8rem; } }

h2, .h2 {
  @include font-extra-bold;
  @include font-h2; }

h3, .h3 {
  @include font-bold;
  @include font-h3; }

h4, .h4 {
  @include font-bold;
  @include font($font-size-h4, $line-height-h4); }

h5, .h5 {
  @include font-bold;
  @include font($font-size-h5, $line-height-h5); }

h6, .h6 {
  @include font-bold;
  @include font($font-size-h6, $line-height-h6);
  text-transform: uppercase; }

.entry-text {
  font-size: 2.4rem;
  line-height: 2.9rem;
  color: $text-color-alt;
  margin-bottom: 3rem;

  @include media('<desk-sm') {
    font-size: 2.2rem;
    line-height: 2.6rem; }

  @include media('<desk-sm') {
    font-size: $font-size--mob;
    line-height: $line-height--mob; }

  &:last-child {
    margin-bottom: 0; }

  b, strong {
    @include font-bold; }

  i, em {
    @include font-bold-italic; } }

.page-control {
  margin-bottom: 4.2rem;
  padding-top: 6rem;

  @include media('<desk-sm') {
    padding-top: 3rem;
    margin-bottom: 3rem; }

  @include media('<tab-sm') {
    display: none;
    padding-top: 1rem;
    margin-bottom: 1rem; } }

.heading {
  margin-bottom: $heading-bottom;

  @include media('<desk-sm') {
    margin-bottom: $heading-bottom--sm; }

  @include media('<tab-sm') {
    margin-top: 3.6rem;
    margin-bottom: 2rem; }

  .entry-text {
    width: 100rem;
    max-width: 100%; } }

p {
  padding: 0;
  margin: 0 0 $bottom-space;

  @include media('<tab-sm') {
    margin-bottom: $bottom-space--mob; } }

hr {
  line-height: 1px;
  height: 0;
  border: none;
  background: none;
  border-bottom: 1px solid $border-color;
  margin-bottom: $bottom-space;

  @include media('<tab-sm') {
    margin-bottom: $bottom-space--mob; } }

a {
  color: $link-color;
  text-decoration: underline;
  cursor: default;
  text-decoration-color: #a9b0c2;
  transition: color $animation-speed;

  &:link {
    cursor: pointer; }

  //&:visited
  //color: #6859ff

  &:hover {
    text-decoration: none; }

  &[href^="javascript:"] {
    cursor: default; } }

.focus {
  &, &:focus {
    outline: 2px solid #4d90fe; } }

img {
  border: none;
  vertical-align: top;
  max-width: 100%;

  table & {
    max-width: none; } }

b, strong, th {
  @include font-bold; }

i, em {
  @include font-italic; }

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
  @include font-bold-italic; }

/* columns */
$columns-gap: 2em;
.columns {
  @include clear; }

.column {
  float: left;
  width: calc(50% - #{round($columns-gap/2)});
  margin: 0 round($columns-gap/2);

  @include media('<mob-lg') {
    float: none;
    width: auto;
    margin: 0 0 5rem;

    &:last-child {
      margin-bottom: 0; } }

  &:nth-child(odd) {
    margin-left: 0; }

  &:nth-child(even) {
    margin-right: 0; } }

/* columns end */

/* table */
table {
  border-collapse: collapse;
  border-spacing: 0;
  width: auto;
  margin-bottom: $bottom-space;

  @include media('<tab-sm') {
    margin-bottom: $bottom-space--mob; }

  td, th {
    @include font($font-size, $line-height);
    color: inherit;
    padding: 8px 15px;
    border-color: $text-color; } }

.table-auto {
  position: relative;
  overflow: auto;
  max-width: 100%;
  -webkit-overflow-scrolling: touch;
  margin-bottom: $bottom-space;

  @include media('<tab-sm') {
    margin-bottom: $bottom-space--mob; }

  table {
    margin-bottom: 0;
    width: auto;
    min-width: 100%; } }

.tbl {
  border: none;

  td, th {
    color: $text-color;
    background-color: $bg-color;
    border: 0 solid $base-color;
    @include font(1.4rem, 1.5);

    &:first-child, &:first-child {
      border-left-width: 1px; }

    &:last-child, &:last-child {
      border-right-width: 1px; } }

  tr:last-child {
    td {
      border-bottom-width: 0; } }

  td {
    border-top-width: 1px; }

  tbody tr:first-child td {
    border-top-width: 0; }

  tbody tr:nth-child(even) td {
    background: $base-color;
    color: $bg-color; }

  th, thead td {
    @include font-bold;
    font-size: 1.6rem;
    text-align: left; }

  th, thead td, tfoot td {
    background: $base-color;
    color: $bg-color;
    border-color: $base-color;
    border-top-width: 0; } }

/* table end */

/* reference link */
.ref {
  display: inline-block;
  padding: 0.5rem 0 0.5rem 4.5rem;
  background: no-repeat 0 50% url('data:image/png;base64,');

  @include media('<tab-sm') {
    padding: 0.5rem 0 0.5rem 3.8rem;
    background-size: auto $line-height--mob + 1; } }

/* reference link end */

/* article elements */
.#{$article-content} {
  padding-bottom: 12.4rem;

  @include media('<tab-lg') {
    padding-bottom: 4.4rem; }

  @each $tags in h2, h3, h4, h5, '.h2', '.h3', '.h4', '.h5' {
    p + #{$tags},
    ul + #{$tags},
    ol + #{$tags},
    hr + #{$tags},
    table + #{$tags},
    .table-auto + #{$tags} {
      margin-top: 6.2rem;

      @include media('<tab-lg') {
        margin-top: 4.5rem; }

      @include media('<tab-sm') {
        margin-top: 3rem; } } }

  // figure
  figure {
    display: block;
    padding: 0;
    margin: 5.3rem 0 5.0rem;

    @include media('<tab-lg') {
      margin: 4.3rem 0 2rem; }

    @include media('<tab-sm') {
      margin: 4rem 0 3rem; }

    img {
      display: block;
      max-width: 100%;
      margin-bottom: 1.7rem;

      @include media('<tab-lg') {
        margin-bottom: 1.5rem; }

      @include media('<tab-sm') {
        margin-bottom: 1.4rem; } } }

  figcaption {
    display: block;
    padding: 0;
    margin: 0;
    color: $text-color-light;
    font-size: 1.8rem;
    line-height: 2.2rem;

    @include media('<tab-lg') {
      font-size: 1.7rem;
      line-height: 2.1rem; }

    @include media('<tab-sm') {
      font-size: 1.4rem;
      line-height: 1.7rem; }

    & > br {
      @include media('<tab-sm') {
        display: none; } } }

  // mark
  & > mark {
    display: block;
    position: relative;
    margin: 2.9rem 0 2.8rem;
    padding: 2.5rem 4rem 2.3rem;
    @include font-bold;
    font-size: 3rem;
    line-height: 3.6rem;
    color: $quote-color;
    text-align: center;
    background: none;

    @include media('<desk-sm') {
      font-size: 2.2rem;
      line-height: 2.6rem; }

    @include media('<tab-sm') {
      margin: 3.6rem 0;
      padding: 2.3rem 0 2.5rem;
      font-size: 2.0rem;
      line-height: 2.4rem; }

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      left: 50%;
      $width: 25.4rem;
      width: $width;
      margin-left: -#{round-float($width / 2, 1)};
      border-top: 2px solid $highlight-color;

      @include media('<tab-lg') {
        $width: 18rem;
        width: $width;
        margin-left: -#{round-float($width / 2, 1)}; } }

    &::before {
      top: 0; }

    &::after {
      bottom: 0; } }

  // blockquote
  blockquote {
    $img-size: 11rem;
    $img-size--sm: 9.6rem;
    $img-space: 2.4rem;
    $img-space--sm: 3.6rem;
    display: block;
    //overflow: hidden
    position: relative;
    margin: 5rem 0;
    padding: 1.7rem 0;
    @include clear;

    @include media('<tab-lg') {
      margin: 3.1rem 0; }

    @include media('<tab-sm') {
      display: flex;
      flex-direction: column;
      margin: 3.6rem 0 3.4rem;
      padding: 3rem 2rem 2.5rem;
      background-color: white; }

    img {
      width: $img-size;
      height: $img-size;
      border-radius: $img-size;
      float: left;
      margin: 0.6rem #{$img-space} -0.6rem 0;

      @include media('<tab-lg') {
        width: $img-size--sm;
        height: $img-size--sm;
        margin: 0.8rem #{$img-space--sm} -0.8rem 0; }

      @include media('<tab-sm') {
        order: 2;
        float: none;
        width: 5.7rem;
        height: 5.7rem;
        margin: 0 auto 1.4rem; } }

    q {
      display: block;
      position: relative;
      margin: 0 0 2rem #{$img-size + $img-space};
      padding: 0 17rem 0 0;
      font-size: 1.8rem;
      line-height: 2.5rem;
      color: $quote-color;

      @include media('<tab-lg') {
        font-size: 2.0rem;
        line-height: 2.5rem;
        margin: 0 0 1.4rem #{$img-size--sm + $img-space--sm};
        padding-right: 10rem; }

      @include media('<tab-sm') {
        order: 1;
        width: 100%;
        text-align: center;
        font-size: 2.0rem;
        line-height: 2.4rem;
        margin: 0 0 2.4rem;
        padding: 4.6rem 0 0; }

      &::after {
        content: "";
        display: block;
        position: absolute;
        right: 4.2rem;
        top: -4.1rem;
        font-size: 32rem;
        line-height: 1;
        color: $highlight-color;
        pointer-events: none;

        @include media('<tab-lg') {
          right: -1.7rem;
          top: -3.5rem; }

        @include media('<tab-sm') {
          right: auto;
          top: -1.9rem;
          width: 100%;
          text-align: center;
          font-size: 15.1rem; } } }

    cite {
      display: block;
      @include font-normal;
      font-style: normal;
      font-size: 1.8rem;
      line-height: 2.5rem;
      color: $text-color-light;
      margin-left: #{$img-size + $img-space};

      @include media('<tab-lg') {
        font-size: 1.9rem;
        margin-left: #{$img-size--sm + $img-space--sm}; }

      @include media('<tab-sm') {
        order: 3;
        width: 100%;
        font-size: 1.4rem;
        line-height: 1.8rem;
        margin: 0;
        text-align: center; } } }

  // list
  $list-space: 3rem !default;
  ul, ol {
    margin: 0 0 $bottom-space;
    padding: 0;
    list-style: none;

    @include media('<tab-sm') {
      margin-bottom: $bottom-space--mob; }

    li {
      padding-left: $list-space;
      margin: 0 0 1.5rem;
      position: relative;

      @include media('<tab-sm') {
        margin-bottom: 2rem; } } }

  ul {
    & > li {
      &::before {
        content: '';
        display: block;
        float: left;
        margin: 0.4em 0 -0.5em -#{$list-space};
        border: 0.5rem solid $highlight-color;
        border-radius: 0.5rem; } } }

  ol {
    $list-space: 3.5rem;
    counter-reset: section;

    & > li {
      counter-increment: section;
      padding-left: $list-space;

      &::before {
        position: relative;
        top: -0.1em;
        display: inline-block;
        content: counter(section);
        height: 2.1rem;
        width: 2.1rem;
        margin: -0.5rem #{$list-space - 2.1rem} -0.5rem -#{$list-space};
        padding: 0.1rem 0 0;
        font-weight: 700;
        font-size: 1.3rem;
        line-height: 2.0rem;
        text-align: center;
        color: white;
        background-color: $highlight-color;
        border-radius: 50%; } } }

  ul, ol {
    ul, ol {
      margin: round-float($bottom-space * 0.5, 1) 0;

      @include media('<tab-sm') {
        margin: round-float($bottom-space--mob * 0.5, 1) 0; } } }

  //      
  //    
  @if nth($page-layout-space, 2) != 0 {
    @include padding-horizontal(nth($page-layout-space, 2)); }

  @include media('<tab-sm') {
    @if nth($page-layout-space--sm, 2) != 0 {
      @include padding-horizontal(nth($page-layout-space--sm, 2)); } }

  @include media('<mob-lg') {
    @if nth($page-layout-space--mob, 2) != 0 {
      @include padding-horizontal(nth($page-layout-space--mob, 2)); } }

  & > h1, & > .h1, .entry-text {
    max-width: $section-layout-width;
    @include push-auto; }

  & > p, & > ul, & > ol,
  & > h2, & > h3, & > h4, & > h5, & > h6,
  & > .h2, & > .h3, & > .h4, & > .h5, & > .h6,
  & > hr, & > mark, & > blockquote, & > figure,
  & > .layout-text {
    max-width: $text-layout-width;
    @include push-auto; }

  & > .layout-flood,
  & > .layout-section {
    max-width: $section-layout-width;
    @include push-auto; }

  & > img,
  & > .layout-text img,
  & > .layout-flood img,
  & > .layout-section img {
    display: block;
    margin: 3rem auto 4rem;

    @include media('<tab-lg') {
      margin-top: 2.5rem;
      margin-bottom: 3rem; }

    @include media('<mob-lg') {
      margin-top: 1.5rem;
      margin-bottom: 2rem; }

    &[align="left"] {
      float: left;
      max-width: 45%;
      margin-right: 3rem;

      @include media('<tab-lg') {
        margin-right: 2rem; }

      @include media('<mob-lg') {
        float: none;
        max-width: none;
        margin-right: auto; } }

    &[align="right"] {
      float: right;
      max-width: 45%;
      margin-left: 3rem;

      @include media('<tab-lg') {
        margin-left: 2rem; }

      @include media('<mob-lg') {
        float: none;
        max-width: none;
        margin-left: auto; } } } }

/* article elements end */
